@font-face {
  font-family: 'LatoWeb';
  src: url('/assets/fonts/Lato-Regular.eot');
  src: url('/assets/fonts/Lato-Regular.eot?#iefix') format('embedded-opentype'),
       url('/assets/fonts/Lato-Regular.woff2') format('woff2'),
       url('/assets/fonts/Lato-Regular.woff') format('woff'),
       url('/assets/fonts/Lato-Regular.ttf') format('truetype');
  font-style: normal;
  font-weight: normal;
  text-rendering: optimizeLegibility;
}
@font-face {
  font-family: 'Adec';
  src: url('/assets/fonts/SS Adec.otf') format('opentype'),
  url('/assets/fonts/SS Adec.ttf') format('truetype');
  font-style: normal;
  text-rendering: optimizeLegibility;
}

:root {
  --color-blue-3: #91d5ff;
  --color-blue-4: #69c0ff;
  --color-blue-5: #40a9ff;
  --color-blue-6: #1890ff;
  --color-blue-7: #096dd9;
  --color-blue-8: #0050b3;
  --color-blue-9: #003a8c;
  --color-blue-10: #002766;

  --color-cyan-5: #36cfc9;
  --color-cyan-6: #13c2c2;
  --color-cyan-7: #08979c;
  --color-cyan-8: #006d75;
  --color-cyan-9: #00474f;
  --color-cyan-10: #002329;

  --color-yellow-1: #feffe6;
  --color-yellow-2: #ffffb8;
  --color-yellow-3: #fffb8f;
  --color-yellow-4: #fff566;
  --color-yellow-5: #ffec3d;
  --color-yellow-6: #fadb14;
  --color-yellow-7: #d4b106;
  --color-yellow-8: #ad8b00;
  --color-yellow-9: #876800;
  --color-yellow-10: #614700;

  --color-volcano-1: #fff2e8;
  --color-volcano-2: #ffd8bf;
  --color-volcano-3: #ffbb96;
  --color-volcano-4: #ff9c6e;
  --color-volcano-5: #ff7a45;
  --color-volcano-6: #fa541c;
  --color-volcano-7: #d4380d;
  --color-volcano-8: #ad2102;
  --color-volcano-9: #871400;
  --color-volcano-10: #610b00;

  --color-lime-5: #bae637;
  --color-lime-6: #a0d911;
  --color-lime-7: #7cb305;
  --color-lime-8: #5b8c00;
  --color-lime-9: #3f6600;
  --color-lime-10: #254000;

  --color-grey-1: #ffffff;
  --color-grey-2: #fafafa;
  --color-grey-3: #f5f5f5;
  --color-grey-4: #e8e8e8;
  --color-grey-5: #d9d9d9;
  --color-grey-6: #bfbfbf;
  --color-grey-7: #8c8c8c;
  --color-grey-8: #595959;
  --color-grey-9: #262626;
  --color-grey-10: #000000;
}

* {
  margin: 0;
  padding: 0;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: LatoWeb, Verdana, sans-serif;
  font-size: 18px;
  line-height: 32px;
  font-style: normal;
  font-weight: 400;
  font-variant-numeric: oldstyle-nums;
}

h1 {
  font-family: Adec, Verdana, sans-serif;
  font-weight: 400;
  letter-spacing: 2px;
  font-size: 72px;
  line-height: 80px;
}

p {
  max-width: 550px;
  min-width: 500px;
}

a {
  text-decoration: none;
  color: var(--color-volcano-5);
}


#root {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}